import React from 'react'
import Input from '../components/input/Input';

import "./invoice.css"
import Select from '../components/select/Select';

const companyValues = [
    {
 value: 'A1 Travels',
 name: 'A1 Travels'
    },
    {
       value: 'JPS Travels',
       name: 'JPS Travels'
   }
]

const billValues = [
    {
      value : "paid" ,
      name: "Paid"
       
    },
    {
        value : "unpaid" ,
        name: "Unaid"
         
      }

]
const collectionValues = [
    {
      value : "nocollect" ,
      name: "To Collect"
       
    },
    {
        value : "collect" ,
        name: "Collected"
         
      }

]


const Invoice = () => {
    return (
        <div>
        <form id="orderForm" name="orderForm" action="sendBillData.php" novalidate="">

		<div className="sub-header">
			<div className="container">
				<div className="option-box">
					<div className="row personalDetails align-items-center fl-form fl-style-1">
                     <Select col="col-md-4" label="Select a company" name="company" id="company" values={companyValues}/>
                        <Input col='col-md-4' label='Sl No' id="slno" name="slno"/>
                        <Input col='col-md-4' label='Ref No' id="ref_no" name="ref_no"/>
					
					</div>
				</div>
			</div>
		</div>
		<main>
			<div className="order pt-0">
				<div className="container">
						<div className="option-box">
							<div className="row">
							
								<div className="col-lg-6 mb-4 mb-lg-0">
									<h5 className="mb-3">To Details</h5>
									<div className="row personalDetails fl-form fl-style-1">
                                    <Input col='col-md-6' label='Name*' id="toname" name="toname"/> 
                                    <Input col='col-md-6' label='Phone No' id="tophone" name="tophone"/> 
                                    <Input col='col-md-6' label='Address' id="toaddress" name="toaddress" /> 
									<Input col='col-md-6' label='GST No' id="to_gst_no" name="to_gst_no"  /> 
									
									</div>
								</div>
								<div className="col-lg-6">
									<h5 className="mb-3">From Details</h5>
									<div className="row personalDetails fl-form fl-style-1">
                                    <Input col='col-md-6' label='Name*' id="fromname" name="fromname" /> 
                                    <Input col='col-md-6' label='Phone No' id="fromphone" name="fromphone"/> 
                                    <Input col='col-md-6' label='Address' id="fromaddress" name="fromaddress" /> 
									<Input col='col-md-6' label='GST No'  id="from_gst_no" name="from_gst_no" /> 
										
									</div>
								</div>
							</div>
						</div>
						<div className="row">		
								
							<div className="col-lg-8">
								<div className="option-box">
							<div className="row">
								<div className="col-lg-12 mb-4 mb-lg-0">
									<h5 className="mb-3">Booking Details</h5>
									<div className="row personalDetails fl-form fl-style-1">
                                    <Input col='col-md-6' label='Luggage Quantity' id="lug_qty" name="lug_qty" /> 
									<Input col='col-md-6' label='Rate*' id="rate" name="rate" /> 
									<Input col='col-md-6' label='Unloading Charging' id="unload_chrg" name="unload_chrg" /> 
                                    <Input col='col-md-6' label='Delivery' id="delivery" name="delivery"/> 
                                    <Select col="col-md-6" label="Bill Status" id="bill_status" name="bill_status" values={billValues}/>

                                    <Select col="col-md-6" label="Collection Status" id="collection_status" name="collection_status" values={collectionValues}/>

									</div>
								</div>
								
							</div>
						</div>
								
					
							</div>
							<div className="col-lg-4" id="">
						
								<div id="orderContainer" className="option-box">
									<div className="row">
										<div className="col-md-12">
											<h5 className="mt-0 mb-3">Order Summary</h5>
											<div className="row total-container">
												<div className="col-6 p-0">
													<input type="text" id="" className="summaryInput" name="totallabel" value="Sub Total" disabled=""/>
												</div>
												<div className="col-6 p-0">

													<input type="text" id="subtotal" className="text-right summaryInput" readonly="" name="subtotal" value="000.00" data-parsley-errors-container="#totalError" data-parsley-empty-order=""/>
												</div>
											</div>
											<div className="row total-container">
												<div className="col-6 p-0 d-flex my-auto align-items-center">
													<label>
													<input type="checkbox" id="gstEnable" data-parsley-multiple="gstEnable"/> GST TAX (%)</label>
												</div>
												<div className="col-6 p-0">
													<input type="text" id="gsttotal" autocomplete="off" className="text-right summaryInput" name="gsttotal" placeholder="0%" data-parsley-errors-container="#totalError" data-parsley-empty-order="" />
													<input type="hidden" id="gstGrandTotal" value="000.00"/>
												</div>
											</div>
											<div className="row total-container">
												<div className="col-6 p-0">
													<input type="text" id="" className="summaryInput" name="totallabel" value="Grand Total" disabled=""/>
												</div>
												<div className="col-6 p-0">
													<input type="text" id="granttotal" className="text-right summaryInput" readonly="" name="granttotal" value="000.00" data-parsley-errors-container="#totalError" data-parsley-empty-order=""/>
												</div>
											</div>
											<div id="totalError"></div>
										</div>
									</div>
									<div className="row">
										<div className="col-lg-12 col-md-5">
											<button type="submit" id="submitButtonHide" name="submit" className="btn-form-func" onclick="submitForm(event)">
												<span className="btn-form-func-content">SUBMIT</span>
												<span className="icon"><i className="fa fa-check" aria-hidden="true"></i></span>
											</button>
										</div>
										<div className="col-lg-12 col-md-5">
											<button type="button" name="reset" id="resetBtn" className="btn-form-func btn-form-func-alt-color" onclick="clearData(event)">
												<span className="btn-form-func-content">RESET</span>
												<span className="icon"><i className="fa fa-times" aria-hidden="true"></i></span>
											</button>
										</div>
									</div>
								</div>
							</div>							
						</div>
				
			
				</div>
			</div>
		
		</main>
	
		</form>
        </div>
    )
}

export default Invoice
