import React,{useRef} from 'react'

import './header.css'
import { Link } from 'react-router-dom' 

import Dropdown from '../dropdown/Dropdown'

import ThemeMenu from '../thememenu/ThemeMenu'

import notifications from '../../assets/JsonData/notification.json'
import user_image from '../../assets/images/tuat.png'
import user_menu from '../../assets/JsonData/user_menus.json'

const curr_user = {
  display_name: 'Tuat Tran',
  image: user_image
}

const renderNotificationItem = (item, index) => (
  <div className="notification-item" key={index}>
      <i className={item.icon}></i>
      <span>{item.content}</span>
  </div>
)

const renderUserToggle = (user) => (
  <div className="topnav__right-user">
      <div className="topnav__right-user__image">
          <img src={user.image} alt="" />
      </div>
      <div className="topnav__right-user__name">
          {user.display_name}
      </div>
  </div>
)

const renderUserMenu =(item, index) => (
  <Link to='/' key={index}>
      <div className="notification-item">
          <i className={item.icon}></i>
          <span>{item.content}</span>
      </div>
  </Link>
)

const Header = () => {
  const hamburger_animarion_ref = useRef(null);
  const hamburger_animarion_mobile_ref = useRef(null);
  const setSidebarMenuActive  = () =>{
    hamburger_animarion_ref.current.classList.contains('open') ? 
    hamburger_animarion_ref.current.classList.remove('open')  :
    hamburger_animarion_ref.current.classList.add('open');

  document.body.classList.contains('isToggle') ? 
  document.body.classList.remove('isToggle') :
  document.body.classList.add('isToggle'); 
  } 
  const setSidebarMobileMenuActive  = () =>{
    hamburger_animarion_mobile_ref.current.classList.contains('open') ? 
    hamburger_animarion_mobile_ref.current.classList.remove('open')  :
    hamburger_animarion_mobile_ref.current.classList.add('open');

    document.body.classList.contains('isToggleMobile') ? 
    document.body.classList.remove('isToggleMobile') :
    document.body.classList.add('isToggleMobile'); 
  } 
  
 
    return (
        <div>
             <header id="header" className="header fixed-top d-flex align-items-center">

<div className="d-flex align-items-center justify-content-between">
  <a href="index.html" className="logo d-flex align-items-center">
    <img src="assets/img/logo.png" alt=""/>
    <span className="d-none d-lg-block">CRM - BILLING</span>
  </a>
  {/* <i className="bi bi-list toggle-sidebar-btn"></i> */}
  {/* <button type="button" className="hamburger close-sidebar-btn hamburger--elastic is-active" data-className="closed-sidebar">
                            <span className="hamburger-box">
                                <span className="hamburger-inner"></span>
                            </span>
                        </button> */}
                        <button ref={hamburger_animarion_ref} onClick={() => setSidebarMenuActive()} type="button" className="hamburger d-none my-auto d-lg-block d-md-block close-sidebar-btn hamburger--elastic is-active" id="nav-icon1">
                          <span></span>
                          <span></span>
                          <span></span>
                        </button>
                        <button ref={hamburger_animarion_mobile_ref} onClick={() => setSidebarMobileMenuActive()} type="button" className="hamburger my-auto d-lg-none d-md-none close-sidebar-btn hamburger--elastic is-active" id="nav-icon2">
                          <span></span>
                          <span></span>
                          <span></span>
                        </button>
</div>

<div className="search-bar">
  <form className="search-form d-flex align-items-center" method="POST" action="#">
    <input type="text" name="query" placeholder="Search" title="Enter search keyword"/>
    <button type="submit" title="Search"><i className="bi bi-search"></i></button>
  </form>
</div>

<nav className="header-nav ms-auto">
  <ul className="d-flex align-items-center">

    <li className="nav-item d-block d-lg-none">
      <a className="nav-link nav-icon search-bar-toggle " href="#">
        <i className="bi bi-search"></i>
      </a>
    </li>

    <li className="nav-item dropdown">

    <Dropdown
          customToggle={() => renderUserToggle(curr_user)}
          contentData={user_menu}
          renderItems={(item, index) => renderUserMenu(item, index)}
                    />

    </li>

    <li className="nav-item dropdown">

    <Dropdown
                        icon='bx bx-bell'
                        badge='12'
                        contentData={notifications}
                        renderItems={(item, index) => renderNotificationItem(item, index)}
                        renderFooter={() => <Link to='/'>View All</Link>}
                    />

    </li>

    <li className="nav-item dropdown pe-3">
    <ThemeMenu/>
    </li>

  </ul>
</nav>

</header>
        </div>
    )
}

export default Header
