import React,{useRef} from 'react'

import { Link } from 'react-router-dom'
import { matchPath,useLocation } from "react-router";

import './sidebar.css'

import sidebar_items from './sidebar_routes.json'


const SidebarItem = props => {

    const active = props.active ? 'active' : ''

    return (
             <Link onClick={() => props.passref()} className={`sidebar__item-inner ${active}`} to={props.route}>
      <i className={`metismenu-icon ${props.icon}`}></i>
      <span>  {props.title}</span>
    </Link>
           
    
    )
}
const clickOutsideSidebarRef = () => {
    debugger;
    let sdfgh = document.getElementById("nav-icon2").classList;
    if(document.getElementById("nav-icon2").classList.contains('open')) {
        document.getElementById("nav-icon2").classList.toggle('open')
        document.body.classList.toggle('isToggleMobile')
    }
   

    // document.body.classList.contains('isToggleMobile') ? 
    // document.body.classList.remove('isToggleMobile') :
    // document.body.classList.add('isToggleMobile'); 
}

const Sidebar = props => {
 
    const location = useLocation();
    const activeItem = sidebar_items.findIndex(item => item.route === location.pathname)

    return (
        <div>
          <aside  id="sidebar" className="sidebar">
<div className="scrollbar-sidebar ps">
<div className="app-sidebar__inner">
{/* <ul class="sidebar-nav" id="sidebar-nav"> */}
    <ul className='vertical-nav-menu metismenu'>
{
                sidebar_items.map((item, index) => (
                        <li key={index} className='app-sidebar__heading'>
               
                        <SidebarItem
                            passref={clickOutsideSidebarRef}
                            title={item.display_name}
                            icon={item.icon}
                            active={index === activeItem}
                            route={item.route}
                        />
                  
                    </li>
                ))
            }
 
</ul>
</div>
</div>
</aside>
        </div>
    )
}

export default Sidebar